import UpdateCamera from './UpdateCamera';
import PointsClickable from './PointsClickable';

const CameraController = (props) => {
    const { camera, gl } = useThree();
    useEffect(() => {
        const controls = new OrbitControls(camera, gl.domElement);

        controls.minDistance = 20;
        controls.maxDistance = 600;
        return () => {
            controls.dispose();
        };
    }, [camera, gl]);
    return null;
    // const { camera, gl } = useThree();
    // useEffect(() => {
    //     const controls = new FlyControls(camera, gl.domElement);
        
    //     controls.movementSpeed = 2500;
    //     controls.rollSpeed = Math.PI / 6;
    //     controls.autoForward = false;
    //     controls.dragToLook = false;

    //     return () => {
    //         controls.dispose();
    //     };
    // }, [camera, gl]);
    // return null;
};

function Scene(props){    
    const { step, COUNT, randomNumArr, startTime, clickedIdx } = props; // step
    // set up camera init here
    return (
        <Canvas camera={{ position: [0, 0, 500] }}>
            <mesh name={"cameraTarget"} visible={false} position={[0, 0, 0]}>
                <boxGeometry args={[1, 1, 1]} />
                <meshBasicMaterial color="black" />
            </mesh>
            <UpdateCamera step={step} />

            <FlashLight />
            <ambientLight color="#555555" />
            <directionalLight color="#ffeedd" position={[0, 0, 1]} />
            <pointLight
                args={[0x062d89, 30, 500, 1.7]}
                position={[200, 300, 100]}
            />
            <CameraController />
            {step > 1 && step < 9 && (
                <Line
                    points={[
                        [-200, -250.0, 0],
                        [200, -250.0, 0]
                    ]}
                    color={"#fff"}
                    lineWidth={0.5}
                    dashed={false}
                />
            )}
            {step < 9 &&
                [1, 3, 4, 5, 6, 7, 8].map((t, i) => {
                    if (step > t) {
                        return (
                            <Text
                                key={`axis-label-text-${t}`}
                                color="#e2e3e4"
                                fontSize={10}
                                anchorX={200 - 64 * i}
                                anchorY={255}
                            >
                                {dateArr[i]}
                            </Text>
                        );
                    }
                    return null;
                })}

            {/* {<Text color={"#fff"} fontSize={1} anchorX={'left'} anchorY={'bottom'} text="Mar. '20"/>} */}
            <Suspense fallback={null}>
                <Points
                    step={step}
                    uNowTime={performance.now()}
                    COUNT={COUNT}
                    randomNumArr={randomNumArr}
                    startTime={startTime}
                />

                <Cloud position={[0, 175, -150]} />
                <Cloud position={[110, 150, -100]} />
                <Cloud position={[-120, 160, -50]} />
                {step > 8 && (
                    <PointsClickable
                        step={step}
                        uNowTime={performance.now()}
                        COUNT={COUNT}
                        randomNumArr={randomNumArr}
                        startTime={startTime}
                        clickedIdx={clickedIdx}
                    />
                )}
            </Suspense>
            {/* <fogExp2 attach="fogExp2" args={["white", 0.002]} /> */}
        </Canvas>
    );
}


export default Scene;
