const RainShaderClickable = {
    vertexShader: `
    #define PI 3.14159265359
    uniform float uTime;
    uniform float uHighlight;
    // uniform float uOldTime;
    // uniform float uNowTime;
    // uniform float uStartTime;
    uniform float uHoverIdx;
    uniform float uLastHighlight;
    attribute vec3 aSpeed;
    attribute vec2 aRandom;

    void main() {
        vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);
        gl_Position = projectionMatrix * mvPosition;
        gl_PointSize =  900.0 / -mvPosition.z + 6. + (aSpeed.x==uHoverIdx? 120.: 20.);
    }
  `,
    fragmentShader: `
    float rand(vec2 co){
      return fract(sin(dot(co.xy ,vec2(12.9898,78.233))) * 43758.5453);
    }
    void main() {
      //0, 119, 182
        // vec3 color = vec3(0., 0.467, 0.714);
        vec3 color = vec3(rand(gl_PointCoord.xy*2.), 0.66, 0.94 +.5* rand(gl_PointCoord.xy*2.0) );
       // vec3 color = vec3(1., 1., 1. );
        vec2 toCenter = (gl_PointCoord.xy - 0.5)* 8.;
        float len = length(toCenter);
        float a = 1.0 - len;
        gl_FragColor = vec4(color, a);
    }
  `
};

export { RainShaderClickable };
