import React, { useRef, useEffect } from "react";
import { useFrame, useLoader } from "@react-three/fiber";
import { TextureLoader } from "three/src/loaders/TextureLoader";
import { stepPoint } from "./stepAttr";
import * as THREE from "three";
import {death_to_date, newDeathsPerWeek} from './data';
import {RainShaderClickable} from './RainShaderClickable';
import {getCurvePosition} from './snoise2d';
import {select} from 'd3-selection';
import deathData from '../data/northjersey_covid_data.json';
import { smoothstep } from "./utilFunctions";

function PointsClickable(props) {
    console.log('pointsClickable run!')
    // This reference will give us direct access to the THREE.Mesh object
    let { step, randomNumArr, clickedIdx } = props;
    let COUNT = 1300;
    const stepNum = !stepPoint[step] ? 0.0 : stepPoint[step]["num"];
    let hoverIdx = 0.

    const mesh = useRef();
    const mat = useRef();
    let timer;
    // Set up state for the hovered and active state
    const [pointTexture] = useLoader(TextureLoader, [
        "./assets/images/raindrop_img.png"
    ]);

    useFrame((state, delta) => {
        const rainDrop = mesh.current;
        const material = mat.current;
        if (rainDrop) {
            material.uniforms.uTime.value = state.clock.elapsedTime;
            // material.uniforms.uOldTime.value = state.clock.oldTime;
            material.uniforms.uHoverIdx.value = hoverIdx;
            const pos = rainDrop.geometry.attributes.position;
            const aProgressParam = rainDrop.geometry.attributes.aProgressParam;
            // change points position
            pos.needsUpdate = true;
            aProgressParam.needsUpdate = true;

            for (let i = 0; i < COUNT; i++) {
                let R = i < 11 ? 300 : 400;
                let t = 2 * Math.PI * randomNumArr[i];
                let u = randomNumArr[i] + randomNumArr[i + COUNT - 1];
                let r = u > 1 ? (2 - u) * R : u * R;

                const x = r * Math.cos(t);
                const z = r * Math.sin(t);
                const newPos = getCurvePosition(
                    x,
                    z,
                    state.clock.elapsedTime,
                    {
                        x: randomNumArr[i],
                        y: randomNumArr[i + COUNT]
                    }
                );
                
                if(clickedIdx && clickedIdx==i){
                    // let progress = exponentialInOut(aProgressParam["array"][i]); 
                    let targetPos = [0, 125, -10];
                    // console.log("progress", aProgressParam["array"][i]);
                    pos["array"][i * 3] = targetPos[0];
                    (pos["array"][i * 3 + 1]<= targetPos[1]) && (pos["array"][i * 3 + 1] += 3);
                    pos["array"][i * 3 + 2] = targetPos[2];
              
                } else {
                    pos["array"][i * 3] = newPos.x;
                    pos["array"][i * 3 + 1] = newPos.y;
                    pos["array"][i * 3 + 2] = newPos.z;
                }                
                
            }
        }
    });

    // when searched a name, this will be called to show tooltip
    let tooltipTimer;
    useEffect(() => {
        if(!clickedIdx && clickedIdx!=0 ) return null;
        clearTimeout(tooltipTimer);
        tooltipTimer = setTimeout(() => {
            let tooltipCtn = select(".tooltip_ctn__100");
            tooltipCtn.classed("active", true);
            tooltipCtn.classed("newActive", true);
            const clickedData = deathData["names"][clickedIdx]
            console.log('clickedData: ', clickedIdx);
            tooltipCtn.style('top', '100px').style('left', '50vw')
            tooltipCtn
                .select(".tooltipHeader")
                .html(clickedData["name_display"]);

            tooltipCtn
                .select(".tooltipLoc")
                .html(
                    `${(clickedData["age"] ? clickedData["age"] + ", " : "") +
                        (clickedData["county"]
                            ? clickedData["county"] + " County"
                            : "")}`
                );
        }, 1200);
        return ()=>{
            clearTimeout(tooltipTimer);
        }
    }, [clickedIdx]);


    // Return the view, these are regular Threejs elements expressed in JSX
    const vertices = [],
        aSpeed = [],
        aRandom = [],
        aProgressParam=[];

    vertices.push(0.0, 100.0, 0.0);
    aSpeed.push(0.001, 0, 0); // index, rank, newDeathsPerWeek
    aRandom.push(0.5, 0.4);

    for (let i = 1; i < COUNT; i++) {
        let R = i < 11 ? 300 : 400;
        let t = 2 * Math.PI * randomNumArr[i];
        let u = randomNumArr[i] + randomNumArr[i + COUNT - 1];
        let r = u > 1 ? (2 - u) * R : u * R;
        
        const x = r * Math.cos(t);
        const y = randomNumArr[i] * 800 - 400;
        const z = r * Math.sin(t);

        let whichWeek = getRank(i, death_to_date);
        // posX, posZ, uTime, aRandom
        // let newPos = getCurvePosition(x, z, 0, {"x": randomNumArr[i], "y": randomNumArr[i + COUNT]})
        // vertices.push(newPos.x, newPos.y, newPos.z);
        vertices.push( (randomNumArr[i]-0.5)* 200, 0, (randomNumArr[i + COUNT]-0.5)* 200 );
        aSpeed.push(i, whichWeek, newDeathsPerWeek[whichWeek]);
        aRandom.push(randomNumArr[i], randomNumArr[i + COUNT]);
        aProgressParam.push(0.001);
    }

    function getRank(idx, arr) {
        let rank = 0;
        for (let i = 0; i < arr.length - 1; i++) {
            if (idx >= arr[i] && idx < arr[i + 1]) {
                rank = arr.indexOf(arr[i + 1]);
            }
        }
        return rank;
    }
    function onPointerOver(event){
        console.log("clickable onPointerOver: ", event.index, event);
        hoverIdx = event.index;
        let tooltipCtn = select(".tooltip_ctn__100");

        let namesData = deathData["names"];
        if (event.index > -1 && namesData[event.index]) {
            document.querySelector('canvas').style.pointer = 'cursor'

            tooltipCtn.classed('active', true);
            tooltipCtn.classed('newActive', true);
            clearTimeout(timer);
            timer = setTimeout(()=>{
                tooltipCtn.classed("newActive", false);
            }, 4000)
            const hovered = namesData[event.index];

            const tooltipCtn2 = document.querySelector(".tooltip_ctn__100");
            tooltipCtn2.style.top = event.pageY-80 +'px';
            tooltipCtn2.style.left = event.pageX-40 + 'px';

            tooltipCtn
                .select(".tooltipHeader")
                .html(hovered["name_display"]);

            tooltipCtn
                .select(".tooltipLoc")
                .html(`${ ( hovered["age"]? (hovered["age"]+', ') : '') + (hovered["county"]? hovered["county"]+ " County": "") }`);                        
        }
    }
    function onPointerOut(event){
        // select(".tooltip_ctn__100").classed('active', false);
         document.querySelector("canvas").style.pointer = "auto";
        return;
    }
    function onDoubleClick(event){
        if(window.innerWidth>=732){
            let data = deathData["names"][event.index];
            let url = data ? data["obit_link"] : "https://lovedandlostnj.com/";
            window.open(url, "_blank");
        }
    }

    return (
        <points
            ref={mesh}
            onDoubleClick={onDoubleClick}
            onPointerOver={onPointerOver}
            onPointerOut={onPointerOut}
        >
            <bufferGeometry attach="geometry">
                <bufferAttribute
                    attachObject={["attributes", "position"]}
                    count={vertices.length / 3}
                    array={new Float32Array(vertices)}
                    itemSize={3}
                    onUpdate={self => {
                        self.needsUpdate = true;
                        self.verticesNeedUpdate = true;
                    }}
                />

                <bufferAttribute
                    attachObject={["attributes", "aSpeed"]}
                    count={aSpeed.length / 3}
                    array={new Float32Array(aSpeed)}
                    itemSize={3}
                />
                <bufferAttribute
                    attachObject={["attributes", "aRandom"]}
                    count={aRandom.length / 2}
                    array={new Float32Array(aRandom)}
                    itemSize={2}
                />

                <bufferAttribute
                    attachObject={["attributes", "aProgressParam"]}
                    count={aProgressParam.length}
                    array={new Float32Array(aProgressParam)}
                    itemSize={1}
                />
            </bufferGeometry>

            <shaderMaterial
                attach="material"
                transparent={true}
                depthTest={false}
                ref={mat}
                args={[RainShaderClickable]}
                blending={THREE.AdditiveBlending}
                uniforms-uTime={{ value: 0 }}
                // uniforms-uOldTime={{ value: 0 }}
                // uniforms-uNowTime={{ value: uNowTime }}
                // uniforms-uStartTime={{ value: startTime }}
                uniforms-pointTexture={{ value: pointTexture }}
                uniforms-uLastHighlight={{
                    value: !step || step < 1 ? 0.0 : stepPoint[step - 1]["num"]
                }}
                uniforms-uClickedIdx={{ value: clickedIdx }}
                uniforms-uHoverIdx={{ value: hoverIdx }}
                uniforms-uHighlight={{ value: stepNum }}
            />
        </points>
    );
}

export default PointsClickable;
